
import XHeader from 'vux/src/components/x-header/index.vue'

import IconMenu from 'components/IconMenu/iconmenu.vue'
export default {
data() {
return {
erroNumber: 0,
lastcode: '',
objdata: {},
showtype: [],
menuList: [{
imgSrc: require('../../imgs/HealthRecords/PhysicalRecords/phy1.png'),
text: "基础信息",
url: "descPhysical",
hide: true,
params: {
type: 1
}
},
{
imgSrc: require('../../imgs/HealthRecords/PhysicalRecords/phy2.png'),
text: "一般情况",
url: "descPhysical",
hide: true,
params: {
type: 2
} // params
},
{
imgSrc: require('../../imgs/HealthRecords/PhysicalRecords/phy3.png'),
text: "生活方式",
url: "descPhysical",
hide: true,
params: {
type: 3
} // params
},
{
imgSrc: require('../../imgs/HealthRecords/PhysicalRecords/phy4.png'),
text: "脏器功能",
url: "descPhysical",
hide: true,
params: {
type: 4
} // params
},
{
imgSrc: require('../../imgs/HealthRecords/PhysicalRecords/phy5.png'),
text: "查体",
url: "descPhysical",
hide: true,
params: {
type: 5
} // params
},
{
imgSrc: require('../../imgs/HealthRecords/PhysicalRecords/phy8.png'),
text: "辅助检查",
url: "descPhysical",
hide: true,
params: {
type: 6
} // params
},
{
imgSrc: require('../../imgs/HealthRecords/PhysicalRecords/phy6.png'),
text: "现存主要健康问题",
url: "descPhysical",
hide: true,
params: {
type: 7
} // params
},
{
imgSrc: require('../../imgs/HealthRecords/PhysicalRecords/phy7.png'),
text: "院治疗情况",
url: "descPhysical",
hide: true,
params: {
type: 8
} // params
},
{
imgSrc: require('../../imgs/HealthRecords/PhysicalRecords/phy9.png'),
text: "非免疫规划预防接种史",
url: "descPhysical",
hide: true,
params: {
type: 9
} // params
},
{
imgSrc: require('../../imgs/HealthRecords/PhysicalRecords/phy10.png'),
text: "健康评价",
url: "descPhysical",
hide: true,
params: {
type: 10
} // params
},
]
}
},
beforeRouteLeave(to, from, next) { // 用于做缓存

if (to.name == 'unusuallist' || to.name == 'descPhysical') {

localStorage.setItem('lastcode', this.$route.params.code || window.localStorage.lastcode)
} else {
localStorage.removeItem('lastcode');
}
next();
},
components: {
XHeader,
IconMenu
},
mounted() {
this.lastcode = this.$route.params.code || window.localStorage.lastcode;
this.initdataico();
},
methods: {

initdataico() {
this.$ajax.post('', {
'MSH.1': 'tjjl',
'MSH.2': 'getPhycialExamInfo',
'TJJLID': this.lastcode,
'token': this.$root.loginInfo.token,
}).then(data => {
console.log(this.$route.params)
let self = this;











if (typeof data != 'undefined' && Object.keys(data).length > 0) { //判断是否空对象
this.objdata = data;
let menuList = this.menuList;

if (this.objdata.jbxxType == 1) {
menuList[0].hide = false;
self.showtype.push(1)
}

if (this.objdata.ybqkType == 1) {
menuList[1].hide = false;
self.showtype.push(2)
}

if (this.objdata.shfsType == 1) {
menuList[2].hide = false;
self.showtype.push(3)
}

if (this.objdata.zqgnType == 1) {
menuList[3].hide = false;
self.showtype.push(4)
}

if (this.objdata.ctType == 1) {
menuList[4].hide = false;
self.showtype.push(5)
}

if (this.objdata.fzType == 1) {
menuList[5].hide = false;
self.showtype.push(6)
}

if (this.objdata.xczywtType == 1) {
menuList[6].hide = false;
self.showtype.push(7)
}

if (this.objdata.zyType == 1) {
menuList[7].hide = false;
self.showtype.push(8)
}

if (this.objdata.jzType == 1) {
menuList[8].hide = false;
self.showtype.push(9)
}

if (this.objdata.jkpjType == 1) {
menuList[9].hide = false;
self.showtype.push(10)
}

for (let i = 0; i < menuList.length; i++) {
const element = menuList[i];
Object.assign(element.params, {
code: data.TJJLID,
showtype: self.showtype
});
}
}
})
},
tounusuallist() { //点击跳转异常
if (this.objdata.YCXZS && this.objdata.YCXZS != 0) {
let code = this.lastcode;
console.log(code)
this.$router.push({
name: "unusuallist",
params: {
code: code,
}
});
}
},
}
}



